#navbar-container{
    background-color: #ffffff;
}
#header-left-section{
    font-size:16px;
    padding-left: 132px;
}
#navbar{
    list-style: none;
    padding:0px;
    font-size: 14px;
    padding-right: 132px;
    line-height: 15px;
}
#header-left-section a,
#navbar > li > a {
    text-decoration: none;
    display: block;
    font-weight: bold;
    color:black;
}
#navbar > li> a{
	padding: 10px 15px;

}
.sticky{
    position: fixed;
    top:0;
    width: 100%
}
