#footer {
  text-align: center;
  padding: 10px 0;
  color: white;
  font-size: 12px;
  background-color: #000;
}

@media (min-width: 520px) {
  #navbar-container,
  #navbar {
    display: flex;
  }
  #navbar-container {
    flex-direction: column;
    align-items: center;
  }
}


@media (max-width: 520px) {
  #header-left-section {
    text-align: center;
  }
}

@media (max-width: 769px) {
  .singleRepoProject,
  .appOnStore {
    flex: 0 0 90%;
  }
  .Skillcontainer {
    flex: 0 0 100%;
    padding-left: 0px;
  }
  #skillsHolder {
    padding: 0 20px;
  }
  #about_container {
    padding: 0 20px;
  }
  #whoamI_aboutme{
    font-size: 60px;
  }
  #projectContainer {
    padding: 0 20px;
}
}
@media (max-width: 920px) {
  .singleRepoProject {
    flex: 0 0 40%;
  }
}

@media (max-width: 969px) {
  #secondPart {
    display: none;
  }
  #firstPart {
    flex-basis: 0;
  }
  #intro_name {
    padding-top: 50px;
  }
  #intro_container {
    justify-content: center;
  }
}
@media (min-width: 1025px) {
  #navbar-container {
    flex-direction: row;
    justify-content: space-between;
  }
}
@media (max-width: 1025px) {
  #header-left-section {
    padding-left: 0px;
  }
}
@media (max-width: 1050px) {
  #navbar {
    padding-right: 0px;
    text-align: center;
  }
}
