#intro_container {
  display: flex;
  flex-direction: row;
  /* height: 90vh; */
  padding: 0 132px;
}
#firstPart {
  flex: 0 0 60%;
}
/* #secondPart {
  padding-top: 50px;
} */
#secondPart > img {
  width: 100%;
  min-width: 400px;
}
#intro_name {
  width: 420px;
  font-family: Montserrat;
  font-size: 45px;
  font-weight: bold;
  text-align: left;
  color: #2b2a2c;
  padding-top: 140px;
}

#light_hello {
  font-size: 32px;
  font-weight: 200;
  color: #a5a5a5;
}
#myName {
  line-height: 1px;
}

.software_engineer {
  width: auto;
  font-family: Montserrat;
  font-size: 16px;
  text-align: center;
  color: #c2c1c7;
  line-height: 1px;
  font-weight: bold;
}
#shareButtons {
  display: flex;
  font-size: 12px;
}
#actionButtons {
  display: flex;
  margin-top:10px;
}
#shareButtons > a {
  text-decoration: none;
  color: black;
  font-weight: bold;
  padding: 0px 6px;
  border: 0.5px solid lightgray;
  border-radius: 10px;
  margin-right: 5px;
}
#shareButtons > a:hover {
  text-decoration: underline;
}
#actionButtons > a {
  text-decoration: none;
  font-weight: bold;
  padding: 0px 50px;
  border: 0.5px solid lightgray;
  border-radius: 10px;
  margin-right: 5px;
}

#hireme{
    color:white;
    background-color:black;
}

#resume{
    color:black;
}
#resume:hover{
  text-decoration: underline;
}
